              <template slot="title">{{ equipment[0] }}</template>
              <el-menu-item index="2-1">{{ equipment[0] }}</el-menu-item>
              <el-menu-item index="2-1">{{ equipment[1] }}</el-menu-item>
              <el-menu-item index="2-2">{{ equipment[2] }}</el-menu-item>
              <el-menu-item index="2-3">{{ equipment[3] }}</el-menu-item>
            </el-submenu>
