      get_tray: "select_tray_Data",
    }),
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
  },
  created() {
    this.get_line();
    this.get_tray();
  },
  mounted() {
    setTimeout(() => {
      var myChart = echarts.init(document.getElementById("main_broken_line"));
      myChart.setOption(this.option_broken_line);
    }, 500);
